/*
 * Vars for the video
 */
.page-content {
  background: #f2ebe4; }
  .page-content .content {
    box-shadow: 0.4rem 0.4rem 0 rgba(0, 0, 0, 0.15); }

.kb-header {
  max-width: 760px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  padding: 0 !important; }
  .mkb-content-main--search .kb-header {
    margin-bottom: 2em; }
  .kb-header .mkb-widget,
  .widget .kb-header {
    padding: 0;
    margin: 0; }
  .elementor-widget-container .kb-header {
    margin-bottom: 0; }

.kb-search {
  border-bottom: 0.1rem solid #D1DFE3;
  padding-bottom: 2rem;
  margin-bottom: 2rem; }
  @media (min-width: 700px) {
    .kb-search {
      padding-bottom: 3rem;
      margin-bottom: 3rem; } }

.kb-search__title {
  font-size: 1.8rem !important;
  line-height: 3rem !important;
  color: #004152 !important;
  font-family: "montserratbold", Helvetica, Arial, sans-serif; }
  .kb-search__title + form {
    margin-top: 0; }

.kb-search__tip {
  color: #52757e !important; }

.mkb-section-title {
  font-size: 3.2rem;
  font-family: "league_gothicregular", Impact, Helvetica, Arial, sans-serif;
  line-height: 36px;
  color: #004152 !important; }
  @media (min-width: 700px) {
    .mkb-section-title {
      font-size: 4.2rem;
      line-height: 44px; } }

/**
 * Client search styles
 */
.kb-search-gradient,
.kb-search-pattern {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0; }

.kb-search-pattern {
  background-repeat: repeat; }

.kb-search {
  position: relative; }
  .kb-search .kb-search__icon,
  .kb-search .kb-search__clear-icon {
    position: absolute;
    font-size: 2rem;
    line-height: 1.5em;
    height: 1.5em;
    top: 0;
    bottom: 0;
    z-index: 2;
    margin: auto;
    font-weight: 300;
    right: 1em;
    color: #4c2974; }
    .kb-search__input-wrap--icons-left .kb-search .kb-search__icon, .kb-search__input-wrap--icons-left
    .kb-search .kb-search__clear-icon {
      right: 1em;
      left: auto; }
  .kb-search .kb-search__clear-icon {
    display: none; }
  .kb-search .kb-search__icon {
    display: block; }
  .kb-search .kb-search__results ul {
    list-style-type: none;
    margin: 0; }

.kb-search__input-wrap {
  display: inline-block;
  position: relative;
  width: 100% !important;
  background: #fff;
  transition: background 150ms ease-in-out; }
  .kb-search__input-wrap--request {
    background: #fafafa; }
  .kb-search__input-wrap--has-content .kb-search__clear-icon {
    display: block; }
  .kb-search__input-wrap--has-content .kb-search__icon {
    display: none; }
  .kb-search__input-wrap--icons-left .kb-search__icon,
  .kb-search__input-wrap--icons-left .kb-search__clear-icon {
    left: 1.5rem;
    right: auto; }

.kb-search__input-wrap--icons-left .kb-search__input {
  padding-left: 8rem; }

.mkb-widget .kb-search__form .kb-search__input-wrap,
.widget .kb-search__form .kb-search__input-wrap {
  margin: 0; }

/**
 * Search results
 */
.kb-search__results-summary {
  position: absolute;
  z-index: 2;
  right: 4em;
  top: 50%;
  margin-top: -0.8em;
  display: block;
  font-size: 0.9em;
  color: #bbbbbb; }
  .kb-search__input-wrap.kb-search__input-wrap--no-results .kb-search__results-summary,
  .kb-search__input-wrap.kb-search__input-wrap--has-results .kb-search__results-summary {
    display: block; }
  .kb-search__input-wrap--icons-left .kb-search__results-summary {
    right: 1.5em; }
  .kb-search__results-summary .kb-search-request-indicator {
    display: none;
    font-size: 1.5em;
    vertical-align: top; }
    .kb-search__input-wrap.kb-search__input-wrap--no-results .kb-search__results-summary .kb-search-request-indicator,
    .kb-search__input-wrap.kb-search__input-wrap--has-results .kb-search__results-summary .kb-search-request-indicator {
      margin-right: 0.25em; }
    .kb-search__input-wrap--request .kb-search__results-summary .kb-search-request-indicator {
      display: inline-block; }
  .kb-search__results-summary .kb-summary-text-holder {
    display: none; }
    .kb-search__input-wrap.kb-search__input-wrap--no-results .kb-search__results-summary .kb-summary-text-holder,
    .kb-search__input-wrap.kb-search__input-wrap--has-results .kb-search__results-summary .kb-summary-text-holder {
      display: inline-block; }

.kb-search__results {
  display: none;
  position: absolute;
  top: 100%;
  left: 2em;
  right: 2em;
  margin-top: -2px;
  max-height: 28em;
  overflow: auto;
  text-align: left;
  background: #fff;
  z-index: 10;
  box-shadow: 2px 2px 1px rgba(0, 0, 0, 0.2); }
  .mkb-widget .kb-search__results,
  .widget .kb-search__results {
    left: 0.8em;
    right: 0.8em; }
  .kb-search__input-wrap.kb-search__input-wrap--has-results .kb-search__results {
    display: block; }
  .kb-search__results ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    border-left: 1px solid rgba(0, 0, 0, 0.1); }
    .kb-search__results ul li {
      line-height: 1.7em;
      margin: 0;
      border-top: 1px solid rgba(0, 0, 0, 0.1); }
      .kb-search__results ul li a {
        display: table;
        table-layout: fixed;
        width: 100%;
        color: #888;
        padding: 0.5em 1em;
        transition: all 150ms ease-in-out;
        outline: none;
        box-shadow: none;
        text-decoration: none;
        border: none; }
        .mkb-widget .kb-search__results ul li a,
        .widget .kb-search__results ul li a {
          display: block; }
        .kb-search__results ul li a:hover, .kb-search__results ul li a:focus, .kb-search__results ul li a:active {
          outline: none;
          color: #333;
          background: #f8f8f8;
          box-shadow: none;
          text-decoration: none;
          border: none; }

.kb-search__result-title {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  float: left;
  max-width: calc(100% - 8em);
  line-height: 1.8em; }
  .kb-search__input-wrap--multiline-results .kb-search__result-title {
    white-space: normal;
    line-height: 1.3em; }
  .mkb-widget .kb-search__result-title,
  .widget .kb-search__result-title {
    max-width: 100%; }

.kb-search__results-info {
  padding: 0.5em 1.2em;
  color: #aaa;
  font-size: 0.8rem;
  text-align: right;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  background: #fafafa; }
  .kb-search__results-info strong {
    color: #777;
    border-bottom: 1px dotted #aaa; }

.kb-search__result-topic {
  white-space: nowrap;
  text-align: right;
  vertical-align: middle;
  float: right;
  width: 8em; }

.kb-search__result-topic-label {
  display: inline-block;
  padding-right: 0.5em;
  font-size: .8em;
  color: #aaa; }

.kb-search__result-topic-name {
  color: #fff;
  background: #4a90e2;
  font-size: .8em;
  line-height: .8em;
  padding: .5em .7em .4em;
  display: inline-block;
  vertical-align: middle;
  border-radius: .4em; }

/* flex layout */
@supports (display: flex) {
  .kb-search__result-header {
    display: flex;
    white-space: nowrap;
    width: 100%; }
  .kb-search__result-title {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    float: none;
    max-width: none; }
    .kb-search__input-wrap--multiline-results .kb-search__result-title {
      white-space: normal; }
    .kb-search__input-wrap--with-excerpt .kb-search__result-title {
      font-weight: 600;
      color: #222529; }
  .kb-search__result-topic {
    text-align: right;
    flex-grow: 1;
    float: none;
    width: auto; } }

.kb-search__tip {
  font-size: 0.9em;
  margin-top: 0.3em;
  color: #aaa; }

.kb-search__result-excerpt {
  padding: 0 1em .5em 0;
  display: block;
  line-height: 1.3em;
  color: #777;
  font-size: 0.9em;
  margin-top: 0.5em; }
